#map {
  width: 90%;
  height: 500px;
  position: relative;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  margin-top: 2%;
}

.amap-icon {
  overflow: visible;
}
.amap-marker-label{
  border: 0;
  background-color: transparent;
}

.info{
  position: relative;
  margin:0;
  top: 0;
  right: 0;
  min-width: 0;
}